//var $ = require("jquery");
//require  module.exports
//import export
import $ from "jquery";
window.$ = $;
import "weui";
//require("weui");
//cnpm install vue
var Vue = require("vue");
//import Vue from "vue";
//cnpm install vue-router
import VueRouter from 'vue-router';
Vue.use(VueRouter);
//组件
var index = require("./components/index.vue")
var home = require("./components/home.vue")
var weui = require("./components/weui.vue")

var router = new VueRouter({
	routes: [{
		path: "/index",
		component: index
	}, {
		path: "/home",
		component: home
	}, {
		path: "/weui",
		component: weui
	}]
})

new Vue({
	el: "#demo",
	template: `
		<div>
			<p>{{name}}</p>
			<a href="#/index">a</a>
			<a href="#/home">b</a>
			<a href="#/weui">c</a>
			<router-view></router-view>
		</div>
	`,
	router,
	data: {
		name: "wscats"
	},
})

