<!--html-->
<template>
	<div>
		<p>{{name}}</p>
		<p>hello world</p>
		<p>456</p>
	</div>
</template>
<!--js-->
<script>
	module.exports = {
		data() {
			return {
				name: "第一页de",
			}
		},
		mounted() {
			$("p").css("color", "red");
		}
	}
</script>
<!--css sass less-->
<style scoped lang="scss">
	$size:100px;
	p {
		font-size: $size;
		background-color: #0000CC;
	}
</style>


